<template>
  <el-dialog
    v-model="visible"
    title="‰∏ä‰º†ÊñáÊ°£"
    width="500px"
  >
    <el-form :model="form" label-width="120px">
      <el-form-item label="ÈÄâÊã©Êñá‰ª∂">
        <el-upload
          ref="uploadRef"
          drag
          action="#"
          :auto-upload="false"
          :on-change="handleFileChange"
          :limit="1"
          class="upload-dragger"
        >
          <el-icon class="el-icon--upload"><upload-filled /></el-icon>
          <div class="el-upload__text">
            ÊãñÊãΩÊñá‰ª∂Âà∞Ê≠§Â§ÑÊàñ <em>ÁÇπÂáª‰∏ä‰º†</em>
          </div>
          <template #tip>
            <div class="el-upload__tip">ÊîØÊåÅ PDF, TXT, DOC, JPG Ê†ºÂºè</div>
          </template>
        </el-upload>
      </el-form-item>
      
      <el-form-item label="ÁõÆÊ†áËôöÊãüÊ®°Âûã">
        <el-select v-model="form.model" placeholder="ÈÄâÊã©Ê®°Âûã">
          <el-option 
            v-for="model in modelStore.enabledModels" 
            :key="model.name" 
            :label="model.name" 
            :value="model.name"
          />
        </el-select>
      </el-form-item>
      
      <el-form-item label="ÂÖ±‰∫´Áü•ËØÜÂ∫ì">
        <el-switch v-model="form.shared" />
      </el-form-item>
      
      <el-form-item label="ÂàÜÊÆµÂ§ßÂ∞è">
        <el-input-number v-model="form.chunk_size" :min="100" :max="2000" :step="100" />
      </el-form-item>
      
      <el-form-item label="ÈáçÂè†Â§ßÂ∞è">
        <el-input-number v-model="form.overlap" :min="0" :max="500" :step="10" />
      </el-form-item>
    </el-form>

    <template #footer>
      <div class="dialog-footer">
        <el-button @click="visible = false">ÂèñÊ∂à</el-button>
        <el-button type="primary" @click="handleUpload" :disabled="!form.file">ÂºÄÂßã‰∏ä‰º†</el-button>
      </div>
    </template>
  </el-dialog>
</template>

<script setup lang="ts">
import { computed, ref } from 'vue'
import { UploadFilled } from '@element-plus/icons-vue'
import { useModelStore } from '@/stores'

const props = defineProps<{
  modelValue: boolean
}>()

const emit = defineEmits<{
  (e: 'update:modelValue', value: boolean): void
  (e: 'upload', data: { file: File; model: string; shared: boolean }): void
}>()

const visible = computed({
  get: () => props.modelValue,
  set: (val) => emit('update:modelValue', val)
})

const modelStore = useModelStore()

const form = ref({
  file: null as File | null,
  model: '',
  shared: false,
  chunk_size: 500,
  overlap: 50
})

const handleFileChange = (file: any) => {
  form.value.file = file.raw
}

const handleUpload = () => {
  if (!form.value.file || !form.value.model) return
  
  emit('upload', {
    file: form.value.file,
    model: form.value.model,
    shared: form.value.shared
  })
  
  // Reset form
  form.value = {
    file: null,
    model: '',
    shared: false,
    chunk_size: 500,
    overlap: 50
  }
}
</script>

